<template>
  <div>
    <div class="headerFixed backgroundNaviBar">
      <van-nav-bar title="实时数据" style="color:white;" @click-left="toBack" left-arrow class="backgroundNaviBar" z-index=0 :border="false"  >
        <!-- <van-icon name="search" slot="right" /> -->
      </van-nav-bar>
      <!-- <mu-appbar style="width: 100%;" class="header backgroundNaviBar">
        <a @click="()=>{
            $router.go(-1)}">
          <mu-button icon slot="left" class="icon" size="20">
            <mu-icon value="keyboard_arrow_left"></mu-icon>
          </mu-button>
        </a>
        <span style="color:white">{{title}}</span>
      </mu-appbar> -->
      <div
        style="color:white;"
        class="backgroundNaviBar"
        :style="showHeaderStyle"
        v-show="showHeader"
        id="showHeader"
      >
        <div class="flexTime flexSpace">
          <div class="timeHeader">
            <span>监测时间</span>
          </div>
        </div>
        <div class="timeBottom">
          <span>{{time}}</span>
        </div>
      </div>
    </div>

    <div class="conatainerContent" :style="margin" style="margin-top:150px;">
      <div v-if="showData">
        <HomeDataTable />
      </div>
      <div v-if="!showData">
        <v-home ref="childComponents" />
      </div>
    </div>
  </div>
</template>
<script>
import HomeDataTable from "@/components/HomeDataTable/index";
import TwoHomeData from "@/views/HomeDataTableTwo/index";
// import toBack from "../../../static/js/navbar/index";
import AppBace from "../../../static/ajax";
export default {
  data() {
    return {
      title: "实时数据",
      showData: false,
      tableStyle1: "",
      tableStyle2: "border:none;color:#ccc",
      time: AppBace.filterTime(new Date()),
      startx: "",
      starty: "",
      showHeader: true,
      margin: "margin-top:150px",
      showHeaderStyle:
        "opacity: 1;filter:Alpha(opacity=100); transition: opacity 2s;"
    };
  },
  components: {
    HomeDataTable,
    "v-home": TwoHomeData
  },
  methods: {
    // 返回
    toBack(){
      
         this.$router.go(-1);
        
    },
    openBotttomSheet() {
      this.showData = !this.showData;
    },
    totableStyle1() {
      this.tableStyle1 = "";
      this.tableStyle2 = "border:none;color:#ccc";
      this.showData = false;
    },
    totableStyle2() {
      this.tableStyle2 = "";
      this.tableStyle1 = "border:none;color:#ccc";
      this.showData = true;
    },
    timeSpot(val) {
      this.time = val;
      console.log(val);
    },
    dataScoll() {
      var startx, starty;
      //获得角度

      //根据起点终点返回方向 1向上 2向下 3向左 4向右 0未滑动

      //手指接触屏幕
      window.addEventListener("touchstart", this.startFun, true);
      //手指离开屏幕
      window.addEventListener("touchend", this.endFunc, true);
    },
    getAngle(angx, angy) {
      return (Math.atan2(angy, angx) * 180) / Math.PI;
    },
    startFun(e) {
      this.startx = e.touches[0].pageX;
      this.starty = e.touches[0].pageY;
    },
    endFunc(e) {
      var endx, endy;
      endx = e.changedTouches[0].pageX;
      endy = e.changedTouches[0].pageY;
      var direction = this.getDirection(this.startx, this.starty, endx, endy);
      switch (direction) {
        case 0:
          console.log("未滑动！");
          break;
        case 1:
          // console.log("向上！");
          // this.showHeader = false;
          this.margin = "margin-top:50px";
          // this.showHeaderStyle =
          // "showHeaderStyle:opacity: 0;filter:Alpha(opacity=0); transition: opacity 2s;";
          $("#showHeader").slideUp(200);
          //  $("#showHeader").slideToggle(200);

          break;
        case 2:
          // console.log("向下！");
          // this.showHeader = true;
          //  $("#showHeader").slideToggle(200);
          $("#showHeader").slideDown(200);
          this.margin = "margin-top:150px";
          break;
        case 3:
          console.log("向左！");
          break;
        case 4:
          console.log("向右！");
          break;
        default:
      }
    },
    getDirection(startx, starty, endx, endy) {
      var angx = endx - startx;
      var angy = endy - starty;
      var result = 0;

      //如果滑动距离太短
      if (Math.abs(angx) < 2 && Math.abs(angy) < 2) {
        return result;
      }

      var angle = this.getAngle(angx, angy);
      if (angle >= -135 && angle <= -45) {
        result = 1;
      } else if (angle > 45 && angle < 135) {
        result = 2;
      } else if (
        (angle >= 135 && angle <= 180) ||
        (angle >= -180 && angle < -135)
      ) {
        result = 3;
      } else if (angle >= -45 && angle <= 45) {
        result = 4;
      }

      return result;
    }
  },
  mounted() {
    // this.dataScoll();
    //手指接触屏幕
    window.addEventListener("touchstart", this.startFun, true);
    //手指离开屏幕
    window.addEventListener("touchend", this.endFunc, true);
  },
  destroyed() {
    window.removeEventListener("touchstart", this.startFun, true);
    window.removeEventListener("touchend", this.endFunc, true);
  }
};
</script>
<style scoped>
.icon img {
  width: 24px;
  height: 24px;
}
.header1 {
  position: fixed;
  top: 0;
}
.conatainerContent {
  /* margin-top: 150px; */
}
.titleHeaderRealData {
  display: flex;
  justify-content: space-between;
  height: 50px;
  align-items: center;
  padding: 0 10px;
}
.headerFixed {
  background-color: #3572fe;
  color: white;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 9999;
}
.titleRealData {
  display: flex;
  justify-content: center;
  font-size: 18px;
}
.flexTime {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.flexSpace {
  justify-content: space-between;
  height: 40px;
  padding: 0 20px;
}
.flexSpace .left {
  border: 1px white solid;
  border-radius: 15px;
  padding: 2px 5px;
  height: 30px;
  margin-right: 10px;
  display: flex;
  align-items: center;
  font-size: 12px;
}
.flexSpace .right {
  border: 1px white solid;
  border-radius: 15px;
  padding: 2px 5px;
  height: 30px;
  display: flex;
  align-items: center;
  font-size: 12px;
}
.timeHeader {
  display: flex;
  align-items: center;
}
.timeBottom {
  height: 60px;
  display: flex;
  /* align-items: center; */
  font-size: 20px;
  padding-left: 20px;
}
#showHeader {
  opacity: 1;
  filter: Alpha(opacity=100);
  transition: opacity 5s;
}
</style>


