<template>
  <div>
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
      <!-- <p>刷新次数: {{ count }}</p> -->
      <div v-for="(item,index) in itemDataCont" :key="index" class="paddingHome">
        <div class="backGround-back border-radius">
          <div class="heaiderDiv">
            <div class="contHeader">
              <span>站点名称</span>
              <span>{{item.station==='中途泵站'?item.station+'(移动监测站)':item.station}}</span>
            </div>
            <!-- <div class="contHeader">
            <span>监测时间</span>
            <span>{{item.datatime}}</span>
            </div>-->
          </div>

          <div class="headerTitle fontWeight">
            <div class="left">因子</div>
            <div class="center">监测值</div>
            <div class="right">标准值</div>
          </div>

          <div class="headerTitle">
            <div class="left">
              <span>pH</span>
            </div>
            <div class="center">
              <span
                  :style="item.ph | filterPhStyle($store.state.factor.ph,$store.state.factor.ph1,item.waterInfo)"
                >{{item.ph | filterNews(huanbao,item.waterInfo,item.devicePh,"ph")}}</span>
              <!-- <span
                :style="item.waterInfo===null||item.systemStatus==4?'color:red':item.ph===null?'':(item.ph>$store.state.factor.ph1||item.ph<$store.state.factor.ph)?'color:red':''"
              >{{item.ph|filter(item.waterInfo,item.systemStatus)}}</span> -->
            </div>
            <div class="right">{{$store.state.factor.ph}}~{{$store.state.factor.ph1}}</div>
          </div>

          <div class="headerTitle" v-show="item.hasCod!==2">
            <div class="left">
              <span>CODcr</span>
            </div>
            <div class="center">
              <span
                  :style="item.cod | filterYellowtToStyle($store.state.factor.cod,item.waterInfo)"
                >{{item.cod|filterNews(huanbao,item.waterInfo,item.deviceCod,"cod")}}</span>
              <!-- <span
                :style="item.waterInfo===null||item.systemStatus==4?'color:red':item.cod===null?'':item.cod>$store.state.factor.cod*1.15?red:item.cod>$store.state.factor.cod&&item.cod<$store.state.factor.cod*1.15?orange:''"
              >{{item.cod|filterOther(item.waterInfo,item.systemStatus)}}</span> -->
            </div>
            <div class="right">{{$store.state.factor.cod+' '+'mg/L'}}</div>
          </div>

          <div class="headerTitle">
            <div class="left">
              <span>氨氮</span>
            </div>
            <div class="center">
               <span
                  :style="item.ammoniaNitrogen | filterYellowtToStyle($store.state.factor.ammoniaNitrogen,item.waterInfo)"
                >{{item.ammoniaNitrogen|filterNews(huanbao,item.waterInfo,item.deviceAn,"ammoniaNitrogen")}}</span>
              <!-- <span
                :style="item.waterInfo===null||item.systemStatus==4?'color:red':item.ammoniaNitrogen===null?'':item.ammoniaNitrogen>$store.state.factor.ammoniaNitrogen*1.15?red:item.ammoniaNitrogen>$store.state.factor.ammoniaNitrogen&&item.ammoniaNitrogen<$store.state.factor.ammoniaNitrogen*1.15?orange:''"
              >{{item.ammoniaNitrogen|filterOther(item.waterInfo,item.systemStatus)}}</span> -->
            </div>
            <div class="right">{{$store.state.factor.ammoniaNitrogen+' '+'mg/L'}}</div>
          </div>

          <div class="headerTitle">
            <div class="left">
              <span>总氮</span>
            </div>
            <div class="center">
               <span
                  :style="item.nitrogen|filterYellowtToStyle($store.state.factor.nitrogen,item.waterInfo)"
                >{{item.nitrogen|filterNews(huanbao,item.waterInfo,item.deviceN,"nitrogen")}}</span>
              <!-- <span
                :style="item.waterInfo===null||item.systemStatus==4?'color:red':item.nitrogen===null?'':item.nitrogen>$store.state.factor.nitrogen*1.15?red:item.nitrogen>$store.state.factor.nitrogen&&item.nitrogen<$store.state.factor.nitrogen*1.15?orange:''"
              >{{item.nitrogen|filterOther(item.waterInfo,item.systemStatus)}}</span> -->
            </div>
            <div class="right">{{$store.state.factor.nitrogen+' '+'mg/L'}}</div>
          </div>

          <div class="headerTitle">
            <div class="left">
              <span>总磷</span>
            </div>
            <div class="center">
              <span
                  :style="item.phosphorus|filterYellowtToStyle($store.state.factor.phosphorus,item.waterInfo)"
                >{{item.phosphorus|filterNews(huanbao,item.waterInfo,item.deviceP,"phosphorus")}}</span>
              <!-- <span
                :style="item.waterInfo===null||item.systemStatus==4?'color:red':item.phosphorus===null?'':item.phosphorus>$store.state.factor.phosphorus*1.15?red:item.phosphorus>$store.state.factor.phosphorus&&item.phosphorus<$store.state.factor.phosphorus*1.15?orange:''"
              >{{item.phosphorus|filterOther(item.waterInfo,item.systemStatus)}}</span> -->
            </div>
            <div class="right">{{$store.state.factor.phosphorus+' '+'mg/L'}}</div>
          </div>

          <div class="headerTitle">
            <div class="left">
              <span>总铜</span>
            </div>
            <div class="center">
              <span
                  :style="item.cuprum | filterWater($store.state.factor.cuprum,item.waterInfo)"
                >{{item.cuprum | filterNews(huanbao,item.waterInfo,item.deviceCu,"cuprum")}}</span>
              <!-- <span
                :style="item.waterInfo===null||item.systemStatus==4?'color:red':item.cuprum===null?'':item.cuprum>$store.state.factor.cuprum?'color:red':''"
              >{{item.cuprum|filterOther(item.waterInfo,item.systemStatus)}}</span> -->
            </div>
            <div class="right">{{$store.state.factor.cuprum+' '+'mg/L'}}</div>
          </div>

          <div class="headerTitle">
            <div class="left">
              <span>总镍</span>
            </div>
            <div class="center">
              <span
                  :style="item.nickel | filterWater($store.state.factor.nickel,item.waterInfo)"
                >{{item.nickel | filterNews(huanbao,item.waterInfo,item.deviceNi,"nickel")}}</span>
              <!-- <span
                :style="item.waterInfo===null||item.systemStatus==4?'color:red':item.nickel===null?'':item.nickel>$store.state.factor.nickel?'color:red':''"
              >{{item.nickel|filterOther(item.waterInfo,item.systemStatus)}}</span> -->
            </div>
            <div class="right">{{$store.state.factor.nickel+' '+'mg/L'}}</div>
          </div>

          <div class="headerTitle" v-if="item.hasCod!==1">
            <div class="left">
              <span>总铬</span>
            </div>
            <div class="center">
               <span
                  :style="item.chromium | filterWater($store.state.factor.chromium,item.waterInfo)"
                >{{item.chromium | filterNews(huanbao,item.waterInfo,item.deviceCr,"chromium")}}</span>
              <!-- <span
                :style="item.waterInfo===null||item.systemStatus==4?'color:red':item.chromium===null?'':item.chromium>$store.state.factor.chromium?'color:red':''"
              >{{item.chromium|filterOther(item.waterInfo,item.systemStatus)}}</span> -->
            </div>
            <div class="right">{{$store.state.factor.chromium+' '+'mg/L'}}</div>
          </div>

          <div class="headerTitle">
            <div class="left">
              <span>总氰化物</span>
            </div>
            <div class="center">
              <span
                  :style="item.prussiate|filterYellowtToStyle($store.state.factor.prussiate,item.waterInfo)"
                >{{item.prussiate|filterNews(huanbao,item.waterInfo,item.devicePr,"prussiate")}}</span>
              <!-- <span
                :style="item.waterInfo===null||item.systemStatus==4?'color:red':item.prussiate===null?'':item.prussiate>$store.state.factor.prussiate*1.15?red:item.prussiate>$store.state.factor.prussiate&&item.prussiate<$store.state.factor.prussiate*1.15?orange:''"
              >{{item.prussiate|filterOther(item.waterInfo,item.systemStatus)}}</span> -->
            </div>
            <div class="right">{{$store.state.factor.prussiate+' '+'mg/L'}}</div>
          </div>
          <div class="headerTitle" v-show="user!=='manager'">
            <div class="left">
              <span>设备状态</span>
            </div>
            <div class="center"></div>
            <div class="right">
              <!-- <span
                :style="(item.systemStatus===0||item.systemStatus===null)?'color:red':'color:green'"
              >{{(item.systemStatus===0||item.systemStatus===null)?'异常':'正常'}}</span>
              <span
                :style="(item.systemStatus===0||item.systemStatus===null)?'color:red':'color:green'"
              >{{(item.systemStatus===0||item.systemStatus===null)?'异常':'正常'}}</span>-->
              <span style v-if="item.systemStatus===1">正常</span>
              <span style="color:red" v-if="item.systemStatus===0">异常</span>
              <span style="color:red" v-if="item.systemStatus===2">维护</span>
              <span style="color:red" v-if="item.systemStatus===3">离线</span>
              <span style="color:red" v-if="item.systemStatus===4">无水</span>
              <span style v-if="item.systemStatus===null">--</span>
            </div>
          </div>
        </div>
      </div>
    </van-pull-refresh>
    <van-overlay :show="showover" @click="showover = false" class="overlay">
      <van-loading type="spinner" color="#1989fa" />
    </van-overlay>
  </div>
</template>
<script>
import AppBace from "../../../static/ajax";
import Vue from "vue";
import { PullRefresh, Overlay, Loading } from "vant";
Vue.use(PullRefresh)
  .use(Overlay)
  .use(Loading);
import "vant/lib/pull-refresh/style";

export default {
  data() {
    return {
      showover: false,
      title: "实时数据",
      itemDataCont: [],
      token: "",
      count: 0,
      isLoading: false,
      user: "",
      red: "color:red",
      orange: "color:orange",
      huanbao:false
    };
  },
  components: {},
  filters: {
     // 因子的显示内容
    filterNews(value, user, water, device, name) {
      //debugger
      //console.log("====",value);
      // 无水跟device的优先级？
      if (water === "无水") {
        return "无水";
      } else {
        if (user == true && device == 4) {
          return "无水";
        } else {
          if (device === 4) {
            return limitFlag[name];
          }
          if (device === 5) {
            return "--";
          }
          // if (user == true) {
          if (value !== null) {
            if (typeof value == "number") {
              return value.toFixed(3);
            } else {
              return value;
            }
          } else {
            return "--";
          }
          // }
        }
      }
    },
    // 不用判断微超标的几个因子
    filterWater(value, standed, water) {
      var color = "";
      if (
        water !== "无水" &&
        water !== "超标" &&
        water !== "黄色" &&
        water !== "-"
      ) {
        if (value > standed) {
          return (color = "color:red");
        } else {
          return "";
        }
      } else if (water === "-") {
        return (color = "");
      } else if (water === "无水") {
       return color = "color:red";
      } else {
        if (value > standed) {
          return (color = "color:red");
        } else {
          return "";
        }
      }
    },
    // 判断微超标的颜色
    filterYellowtToStyle(value, standed, water) {
      var color = "";
      if (
        water !== "无水" &&
        water !== "超标" &&
        water !== "黄色" &&
        water !== "-"
      ) {
        if (value > standed && value < standed * 1.15) {
          return (color = "color:orange");
        } else if (value > standed * 1.15) {
          return (color = "color:red");
        } else {
          return "";
        }
      } else if (water === "-") {
        return (color = "");
      } else if (water === "无水") {
       return color = "color:red";
      } else {
        if (value > standed && value < standed * 1.15) {
          return (color = "color:orange");
        } else if (value > standed * 1.15) {
          return (color = "color:red");
        } else {
          return "";
        }
      }
    },
    // 判断ph的颜色
    filterPhStyle(value, small, large, water) {
      var color = "";
      if (
        water !== "无水" &&
        water !== "超标" &&
        water !== "黄色" &&
        water !== "-"
      ) {
        if (value!==null) {
          if (value < small || value > large) {
          return (color = "color:red");
        } else {
          return "";
        }
        }else{
          return ""
        }
      } else if (water === "-") {
        return (color = "");
      } else if (water === "无水") {
       return color = "color:red";
      } else {
       if (value!==null) {
          if (value < small || value > large) {
          return (color = "color:red");
        } else {
          return "";
        }
       }else{
         return "--"
       }
      }
    },
    filter(value, water,systemStatus) {
      //debugger
      //console.log("====",value);
      if (water !== null&&water !== "-"&&systemStatus!==4) {
        if (value !== null) {
          if (typeof value == "number") {
            return value.toFixed(3);
          } else {
            return value;
          }
        } else {
          return "--";
        }
      }else if(water==null||systemStatus==4){
        return "无水";
      }
      else{
        return "--";
      }
    },
    filterOther(value, water,systemStatus) {
      // console.log("value",value);
      // console.log("water",water);
      if (water !== null&&water !== "-"&&systemStatus!==4) {
        if (value !== null) {
          if (typeof value == "number") {
            return value.toFixed(3) + "mg/L";
          } else {
            return value;
          }
        } else {
          return "--";
        }
      } else if(water==null||systemStatus==4){
        return "无水"
      }else{
        return "--"
      }
    }
  },
  methods: {
    onRefresh() {
      setTimeout(() => {
        this.$toast("刷新成功");
        this.isLoading = false;
        // this.count++;
        this.initData();
      }, 500);
    },
    openBotttomSheet() {},
    async initData() {
      var url = "/station/monitor-system/datas/app";
      this.token = sessionStorage.getItem("token");
      var res = await AppBace.request(url, "GET", {}, this.token);
      //   this.list = this.spliceString(res.data.data);
      //   this.number = (this.list.length + 1) * 42 + 5;
      this.itemDataCont = res.data.data;
      console.log("实时数据", res);
      this.showover = false;
      this.$parent.timeSpot(this.itemDataCont[0].datatime);
    }
  },
  mounted() {
    this.user = sessionStorage.getItem("user");
    if (this.user==="manager") {
      this.huanbao = true;
    }else{
     this.huanbao = false;
    }
    this.showover = true;
    this.initData();
  }
};
</script>
<style scoped>
.icon img {
  width: 26px;
  height: 26px;
}
.backGround-back {
  background-color: white;
  /* border-radius: 2px; */
  padding: 10px;
}
.paddingHome {
  padding: 5px 10px 5px 10px;
  border-radius: 10px;
}
.contHeader {
  font-weight: 600;
  font-size: 14px;
  height: 40px;
  /* border-bottom: 1px solid #ccc; */
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.contData {
  height: 40px;
  flex: 3;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 12px;
  padding: 10px 5px;
}
.flexContent {
  display: flex;
  flex-wrap: wrap;
}
.line {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #ccc;
}
.headerTitle {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 40px;
  font-size: 12px;
}
.headerTitle .left {
  /* width: 33.33%; */
  flex: 1;
  display: flex;
  justify-content: center;
}
.headerTitle .center {
  flex: 4;
  display: flex;
  justify-content: center;
}
.headerTitle .right {
  text-align: right;
  flex: 1;
  display: flex;
  justify-content: center;
}
.heaiderDiv {
  border-bottom: 1px solid rgb(240, 240, 240);
}
.fontWeight {
  font-weight: 600;
  font-size: 12px;
}
</style>


