<template>
  <div>
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
      <p>刷新次数: {{ count }}</p>

      <div class="HomeFlexScope">
        <div class="flex" style="width:24%">
          <div class="Flex">
            <div class="flexRow widthContent">
              <span style="background:#d4e2fe;font-weight:600" class="spanWidth widthContent">水站名称</span>
              <!-- <span style="background:#d4e2fe;font-weight:600" class="spanWidth widthContent">时间</span> -->
            </div>
            <div
              class="flexRow"
              style="background:white;"
              v-for="(item,id) in list"
              :key="id"
              @click="ToHistory(item.station,item.cod,item.type)"
            >
              <span
                class="spanWidth widthContent fontSizeCont"
                :style="(id%2===0)?'background:#f6f6f6;':'background:#fff;font-size:14px'"
              >{{item.station}}</span>
              <!-- <span
              class="spanWidth widthContent fontSizeCont"
              :style="(id%2===0)?'background:#f6f6f6':'background:#fff'"
              >{{item.datatime===null?'--':item.datatime.slice(5,16)}}</span>-->
              <!-- (item.ph===null)?"--": -->
            </div>
          </div>
        </div>
        <div style="width:76%;">
          <mu-grid-list class="gridlist-inline-demo" :cols="4" :cell-height="number">
            <mu-grid-tile style="text-align:center;padding:1px;">
              <div class="flex" style="background:#d4e2fe;font-weight:600">
                <!-- <span class="spanWidth">时间</span> -->
                <span class="spanWidth">pH</span>
                <span class="spanWidth">氨氮mg/L</span>
                <span class="spanWidth">总氮mg/L</span>
                <span class="spanWidth">总磷mg/L</span>
                <span class="spanWidth">总铜mg/L</span>
                <span class="spanWidth">总镍mg/L</span>
                <span class="spanWidth">总铬mg/L</span>
                <span class="spanWidth">总氰化物mg/L</span>
                <span class="spanWidth">CODcr mg/L</span>
                <span class="spanWidth">设备状态</span>
              </div>

              <div v-for="(item,id) in list" :key="id">
                <div class="flex1" :style="(id%2===0)?'background:#f6f6f6':'background:#fff'">
                  <!-- 时间 -->
                  <!-- <span class="spanWidth">{{(item.ph===null)?"--":item.datatime.slice(5,16)}}</span> -->
                  <!-- ph -->
                  <span
                    class="spanWidth fontSizeCont"
                    style="color:red"
                    v-if="item.ph<$store.state.factor.ph"
                  >{{(item.ph===null)?"--":item.ph}}</span>
                  <span
                    class="spanWidth fontSizeCont"
                    v-if="item.ph>=$store.state.factor.ph&&item.ph<=$store.state.factor.ph1"
                  >{{item.ph}}</span>
                  <span
                    class="spanWidth fontSizeCont"
                    style="color:red"
                    v-if="item.ph>$store.state.factor.ph1"
                  >{{item.ph}}</span>
                  <!-- 氨氮 -->
                  <span
                    class="spanWidth fontSizeCont"
                    style="color:red"
                    v-if="item.ammoniaNitrogen>$store.state.factor.ammoniaNitrogen"
                  >{{item.ammoniaNitrogen}}</span>
                  <span
                    class="spanWidth fontSizeCont"
                    v-if="item.ammoniaNitrogen<=$store.state.factor.ammoniaNitrogen"
                  >{{(item.ammoniaNitrogen===null)?"--":item.ammoniaNitrogen}}</span>
                  <!-- 总氮 -->
                  <span
                    class="spanWidth fontSizeCont"
                    style="color:red"
                    v-if="item.nitrogen>$store.state.factor.nitrogen"
                  >{{item.nitrogen}}</span>
                  <span
                    class="spanWidth fontSizeCont"
                    v-if="item.nitrogen<=$store.state.factor.nitrogen"
                  >{{(item.nitrogen===null)?"--":item.nitrogen}}</span>
                  <!-- 总磷 -->
                  <span
                    class="spanWidth fontSizeCont"
                    style="color:red"
                    v-if="item.phosphorus>$store.state.factor.phosphorus"
                  >{{item.phosphorus}}</span>
                  <span
                    class="spanWidth fontSizeCont"
                    v-if="item.phosphorus<=$store.state.factor.phosphorus"
                  >{{(item.phosphorus===null)?"--":item.phosphorus}}</span>
                  <!-- 总铜 -->
                  <span
                    class="spanWidth fontSizeCont"
                    style="color:red"
                    v-if="item.cuprum>1"
                  >{{item.cuprum}}</span>
                  <span
                    class="spanWidth fontSizeCont"
                    v-if="item.cuprum<=$store.state.factor.cuprum"
                  >{{(item.cuprum===null)?"--":item.cuprum}}</span>
                  <!-- 总镍 -->
                  <span
                    class="spanWidth fontSizeCont"
                    style="color:red"
                    v-if="item.nickel>$store.state.factor.nickel"
                  >{{item.nickel}}</span>
                  <span
                    class="spanWidth fontSizeCont"
                    v-if="item.nickel<=$store.state.factor.nickel"
                  >{{(item.nickel===null)?"--":item.nickel}}</span>
                  <!-- 总铬 -->
                  <span
                    class="spanWidth fontSizeCont"
                    style="color:red"
                    v-if="item.chromium>$store.state.factor.chromium"
                  >{{item.chromium}}</span>
                  <span
                    class="spanWidth fontSizeCont"
                    v-if="item.chromium<=$store.state.factor.chromium"
                  >{{(item.chromium===null)?"--":item.chromium}}</span>
                  <!-- 总氰化物 -->
                  <span
                    class="spanWidth fontSizeCont"
                    style="color:red"
                    v-if="item.prussiate>$store.state.factor.prussiate"
                  >{{item.prussiate}}</span>
                  <span
                    class="spanWidth fontSizeCont"
                    v-if="item.prussiate<=$store.state.factor.prussiate"
                  >{{(item.prussiate===null)?"--":item.prussiate}}</span>
                  <!-- 化学含氧量 -->
                  <span
                    class="spanWidth fontSizeCont"
                    style="color:red"
                    v-if="item.cod>$store.state.factor.cod"
                  >{{item.cod}}</span>
                  <span
                    class="spanWidth fontSizeCont"
                    v-if="item.cod<=$store.state.factor.cod"
                  >{{(item.cod===null)?"--":item.cod}}</span>
                  <!-- 状态 -->
                  <span class="spanWidth fontSizeCont" v-if="item.systemStatus===null">--</span>
                  <span
                    class="spanWidth fontSizeCont"
                    v-if="item.systemStatus===0"
                    style="color:red"
                  >异常</span>
                  <span
                    class="spanWidth fontSizeCont"
                    v-if="item.systemStatus==1"
                    style="color:green"
                  >正常</span>
                </div>
              </div>
            </mu-grid-tile>
          </mu-grid-list>
        </div>
      </div>
    </van-pull-refresh>
  </div>
</template>
<script>
import AppBace from "../../../static/ajax";
import Vue from "vue";
import { PullRefresh } from "vant";
Vue.use(PullRefresh);
export default {
  name: "HomeDataTable",
  data() {
    return {
      list: [],
      number: 400,
      token: "",
      count: 0,
      isLoading: false
    };
  },
  methods: {
    async initData() {
      var url = "/station/monitor-system/datas/app";
      this.token = sessionStorage.getItem("token");
      var res = await AppBace.request(url, "GET", {}, this.token);
      // debugger
      this.list = this.spliceString(res.data.data);
      this.number = (this.list.length + 1) * 42 + 5;
      console.log("实时数据", res);
    },
    spliceString(e) {
      for (let index = 0; index < e.length; index++) {
        var element = e[index].station;
        e[index].station = element.slice(0, 7);
      }
      return e;
    },
    ToHistory(index, codValue, type) {
      var user = sessionStorage.getItem("user");
      if (user !== null) {
        this.$router.push({
          path: "/ToHistory",
          query: { waterStation: index, codStatus: codValue, type: type }
        });
      }
    },
    onRefresh() {
      setTimeout(() => {
        this.$toast("刷新成功");
        this.isLoading = false;
        this.count++;
      }, 500);
    }
  },
  mounted() {
    this.initData();
  }
};
</script>
<style scoped>
.spanWidth {
  width: 100px;
  height: 42px;
  line-height: 42px;
  text-align: center;
  border-bottom: 1px rgb(236, 235, 235) solid;
}
.flex1 {
  display: flex;
  flex-direction: row;
  /* margin-bottom: 60px; */
}
/* 字体色 #1d905b 背景 e8fff3 */
.flex {
  display: flex;
  flex-direction: row;
}
.Flex {
  display: flex;
  flex-direction: column;
  width: 100%;
  /* height: 40px; */
  /* align-items: center;     */
  /* justify-content: center; */
}
.HomeTableTitle {
  display: flex;
  flex-direction: row;
}
.RollList {
  display: flex;
  flex-wrap: nowrap;
  overflow: auto;
  width: 100%;
}
.RollList div {
  width: 100px;
}
.HomeFlexScope {
  display: flex;
  flex-direction: row;
  width: 100%;
  /* margin-bottom: 70px; */
  background: white;
}
.gridlist-inline-demo {
  display: flex;
  flex-wrap: nowrap;
  overflow-x: auto;
}
.widthName {
  width: 180px;
}
.flexRow {
  display: flex;
  flex-direction: row;
}
.widthContent {
  width: 100%;
}
.fontSizeCont {
  /* font-size: 14px; */
}
</style>



